/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(210, 67%, 99%);
  --clr-primary-2: hsl(185, 84%, 25%);
  --clr-primary-3: hsl(185, 81%, 29%);
  --clr-primary-4: hsl(184, 77%, 34%);
  --clr-primary-5: #2caeba;
  /* lighter shades of primary color */
  --clr-primary-6: hsl(185, 57%, 50%);
  --clr-primary-7: hsl(184, 65%, 59%);
  --clr-primary-8: hsl(184, 80%, 74%);
  --clr-primary-9: hsl(185, 94%, 87%);
  --clr-primary-10: hsl(186, 100%, 94%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(241, 89%, 18%);
  --clr-grey-2: hsl(241, 69%, 50%);
  --clr-grey-3: hsl(241, 76%, 24%);
  /* --clr-grey-3: hsl(209, 34%, 30%); */
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(225, 20%, 61%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(8, 100%, 63%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}
/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  /* background: var(--clr-grey-1); */
  /* color: var(--clr-primary-1); */
  line-height: 1.5;
  font-size: 0.875rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  /* text-transform: capitalize; */
  /* line-height: 1.25; */
  line-height: 1;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 1.5rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.975rem;
}
p {
  font-size: 1.15rem;
  margin-bottom: 1.25rem;
  color: var(--clr-primary-1);
}
@media screen and (min-width: 800px) {
  h1 {
    /* font-size: 4rem; */
    font-size: 3.2rem;
  }
  h2 {
    /* font-size: 2.5rem; */
    font-size: 1.5rem;
  }
  h3 {
    font-size: 1.2rem;
  }
  h4 {
    font-size: 1.1rem;
  }
  p {
    /* font-size: 0.95rem; */
    font-size: 1rem;
  }

  body {
    /* font-size: 0.8rem; */
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}

.video-container {
  /* border: 3px solid green; */
  cursor: pointer;
  margin: 0rem auto;
  padding: 1rem;
  max-width: 60rem;
}

.video-container img {
  display: block;
  width: 100%;
  margin-bottom: 1rem;
}

.video-text {
  /* border: 2px solid red; */
}

.video-detail {
  /* border: 3px solid green; */
  cursor: pointer;
  margin: 1rem auto;
  padding: 1rem;
  max-width: 60rem;
}

.video-detail iframe {
  height: 15rem;
  /* height: auto; */
}

form {
  /* border: 1px solid blue; */
}

input {
  width: 10rem;
  height: 2rem;
}

.search-div {
  /* border: 5px solid blue; */
  margin: 0 auto;
  margin-bottom: 3rem;
  max-width: 60rem;
  display: flex;
  justify-content: start;
  align-items: center;
}

.search-div h2 {
  /* border: 1px solid red; */
  align-self: center;
  margin-top: 0.7rem;
  margin-left: 0.5rem;
}

.search-div form {
  margin-left: 2rem;
}

.search-div button {
  background: grey;
  color: #fff;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
}

@media screen and (min-width: 450px) {
  input {
    width: 15rem;
    height: 2rem;
  }
}

@media screen and (min-width: 650px) {
  .video-detail iframe {
    height: 25rem;
  }
  input {
    width: 25rem;
    height: 2rem;
  }
  .search-div form {
    margin-left: 6rem;
  }
}

@media screen and (min-width: 800px) {
  .video-container {
    display: flex;
  }

  .video-container img {
    max-width: 20rem;
  }

  .video-text {
    margin-left: 1rem;
  }

  .search-div form {
    margin-left: 9rem;
  }
}

@media screen and (min-width: 650px) {
  .video-detail iframe {
    height: 30rem;
  }
}
